"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Loader2, Lock, Mail, ArrowRight } from "lucide-react"

export default function LoginPage() {
  const router = useRouter()
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    try {
      const response = await fetch("http://34.50.7.8:3000/v1/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      })

      const data = await response.json()

      if (response.ok && data.isSuccess) {
        // 1. 대시보드에서 사용할 필수 정보만 저장
        localStorage.setItem("accessToken", data.result.accessToken)
        localStorage.setItem("userId", data.result.userId)
        localStorage.setItem("name", data.result.name)

        // 2. 로그인 성공 시 대시보드로 이동
        // 계좌 정보 조회는 대시보드 페이지가 로드될 때 실행됩니다.
        router.push("/dashboard")
      } else {
        alert(data.message || "로그인 정보를 확인해주세요.")
      }
    } catch (error) {
      console.error("Login Error:", error)
      alert("서버 연결에 실패했습니다.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-background p-4 relative overflow-hidden">
      {/* 배경 장식 원 (기존 디자인 요소) */}
      <div className="absolute top-[-10%] right-[-10%] w-[40%] h-[40%] bg-primary/5 rounded-full blur-3xl" />
      <div className="absolute bottom-[-10%] left-[-10%] w-[40%] h-[40%] bg-primary/5 rounded-full blur-3xl" />

      <Card className="w-full max-w-md p-8 shadow-2xl border-primary/10 backdrop-blur-sm bg-card/80 z-10">
        <div className="flex flex-col items-center mb-8">
          <div className="mb-6">
            <Image src="/logo.png" alt="Next Me" width={140} height={40} className="h-9 w-auto" priority />
          </div>
          <h2 className="text-xl font-bold tracking-tight">반가워요! 다시 오셨군요</h2>
          <p className="text-sm text-muted-foreground mt-1">안전한 자산 관리를 위해 로그인하세요</p>
        </div>

        <form onSubmit={handleLogin} className="space-y-5">
          <div className="space-y-2">
            <label className="text-xs font-black uppercase tracking-widest text-muted-foreground ml-1">Email</label>
            <div className="relative">
              <Mail className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                type="email"
                placeholder="example@email.com"
                className="pl-10 h-12 bg-background/50 focus:bg-background transition-all"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <label className="text-xs font-black uppercase tracking-widest text-muted-foreground ml-1">Password</label>
            <div className="relative">
              <Lock className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                type="password"
                placeholder="••••••••"
                className="pl-10 h-12 bg-background/50 focus:bg-background transition-all"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
          </div>

          <Button type="submit" className="w-full h-12 font-bold text-lg shadow-lg shadow-primary/20" disabled={isLoading}>
            {isLoading ? (
              <Loader2 className="h-5 w-5 animate-spin mr-2" />
            ) : (
              <>
                로그인 <ArrowRight className="ml-2 h-5 w-5" />
              </>
            )}
          </Button>
        </form>

        <div className="mt-8 pt-6 border-t border-border/50 text-center">
          <p className="text-sm text-muted-foreground">
            아직 회원이 아니신가요?{" "}
            <Link href="/register" className="text-primary font-bold hover:underline underline-offset-4">
              무료 회원가입
            </Link>
          </p>
        </div>
      </Card>
    </div>
  )
}